body {
  margin: 0;
  padding: 0;
}
.random-title{
	margin-top: 12px;
	min-height: 34px;
	color: #aed8ea;
	letter-spacing: 1.6px; 
	font-size: 24px;
	font-weight: bold;
	font-family: 'Anton', sans-serif;
	text-align: center;
	}
.content-wrapper{
	top: 50px;
	margin:0 auto;
	position: relative;
}

.Dropdown-title {
	color: red;
}
.content-wrapper .content-inner{
	justify-content:space-between;
	display:flex;
	margin-bottom:10px;
}

.content-wrapper .Dropdown-control{
	background-color:transparent !important;
	border: none;
	color:red;
	font-family: Proxima Nova Bold,Helvetica Neue,Helvetica,Arial,sans-serif;
	font-size: 20px;
	position: relative;
	box-sizing: border-box;	
	text-transform: uppercase;
	font-weight: 600;
	letter-spacing: 1.5px;
	padding: 0px 24px  0px;
}

.Dropdown-control{
	color:red;
}
.Dropdown-menu{
	border-radius: 6px;
	background-color: #53565d !important;
	border:none !important;
	padding-top: 5px;
	padding-bottom: 13px;
}
 .is-selected{
	color: white!important;
	background-color: transparent !important;
}
.Dropdown-option:hover{
	background-color:hsla(0,0%,80%,.29) !important;
}
.Dropdown-option{
	padding-left: 18px !important;
	padding-right: 26px !important;
	color: #babec4;
	font-weight: bold;
}
.Dropdown-arrow {
	position: absolute;
    right: 0px;
    top: 10px !important;
}



/*VICTOR RESPONSIVE*/
@media screen and (min-width: 900px){
	.content-wrapper{
		width:90%;
	}
	.tag-container{
	width:100%;
	}
}
