.gridsub_column{
	height: 80px;
	background-color: transparent;
	border-radius: 4px;
	list-style-type: none;
	display: inline-flex;
	margin-bottom: 2px;
	position: relative;
	padding: 8px;
	box-sizing: border-box;
	width:300px;
}
.popular_wrapper{
	width:300px;
	height: 410px;
	box-sizing: border-box;
   

}
.popular_wrapper ul{
	padding: 0;
	margin: 0;

}
.gridsub_column li{
	display: flex;
}
.post{
	height: 80px;
	display: block;
	margin: -8px 0 -8px -8px;
	box-sizing: border-box;
	width:113px;
}
.post img{
	max-width:113px;
	height: 80px;
	position: relative;
}
.title{
	width:167px;
	height: 66px;
	position: relative;
	margin-left: 6px;
}
.title .list-title{
	height: 58px;
	margin: 0 auto;
	font-size: 14px;
	line-height: 1.3;
	padding-top: 4px;
	overflow: hidden;
	text-overflow: ellipsis;
	font-family: Proxima Nova Bold,Helvetica Neue,Helvetica,Arial,sans-serif;
	font-weight: bold;
	color:#f2f2f2;
}
